.flipper {
  transition: 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
}

.card.komplekskort.face-down {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  min-height: 250px;
  color: #fff;
  font-size: 150px;
  background-color: #39cccc;
}

.card.komplekskort.face-up {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  min-height: 250px;
  color: #fff;
  font-size: 35px;
  background-color: #39cccc;
  /* for firefox 31 */
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.card.komplekskort .large-margin {
  padding-top: 50px;
  margin: 70px;
}
@media only screen and (max-width: 768px) {
  .card.komplekskort .large-margin {
    padding-top: 30px;
    margin: 10px;
  }
  .card.komplekskort.face-down {
    min-height: 100px;
    font-size: 60px;
  }
  .card.komplekskort.face-up {
    min-height: 100px;
  }
}

.segment.basic.action_buttons {
  margin-top: 10px;
}

.buttons.action_buttons {
  margin-top: 20px;
}
div.ui.segment.center.aligned.top.attached > h1 {
  text-transform: capitalize;
}
div.ui.segment.center.aligned.top.attached > h1 > div {
  text-transform: none;
}

